<!-- eslint-disable max-len -->
<!-- eslint-disable vuejs-accessibility/anchor-has-content -->
<template>
  <section class="card-group row">
    <div class="card col-2 d-none d-sm-block">
      <div class="card-body d-flex flex-column align-items-center justify-content-between h-100">
        <a
          href="#"
          class="text-decoration-none h4 position-relative"
          @click.prevent="language='chinese'"
          >中文
          <Circle2 :class="{'d-none': language!=='chinese'}" />
        </a>
        <a
          href="#"
          class="text-decoration-none h4 position-relative"
          @click.prevent="language='english'"
          >ENGLISH<Circle3 :class="{'d-none': language!=='english'}" />
          </a>
        <a
          href="#"
          class="text-decoration-none h4 position-relative"
          @click.prevent="language='korean'"
          >한국어<Circle1 :class="{'d-none': language!=='korean'}" />
          </a>
      </div>
    </div>
    <div class="card col-8">
      <div class="card-body">
        <router-link to="/" class="d-flex flex-column align-items-center text-decoration-none">
          <h5 class="titleText font-monospace fw-bold mb-0">
            {{ today }}, {{ dateMonth.toUpperCase() }} {{ date }}, {{ dateYears }}
          </h5>
          <h2 class="titleText font-monospace fw-bold mb-0">LULU BAKE</h2>
          <h4 class="mb-0 font-monospace fw-bold titleText">PIE & CAKE STORE</h4>
        </router-link>
      </div>
    </div>
    <div class="card col-2 d-none d-sm-block">
      <div class="card-body d-flex flex-column justify-content-center align-items-center h-100">
        <a href="#" @click.prevent="this.$refs.cartModal.toggleModal()">
          <i class="bi bi-basket2 h2 position-relative">
            <span
              class="position-absolute top-0 start-100 translate-middle badge rounded-pill text-secondary fs-5"
            >
              9+
            </span>
          </i>
        </a>
      </div>
    </div>
  </section>
  <Cart ref="cartModal" />
</template>

<style lang="scss" scoped>
.circle-1 {
  width: 8rem;
}
.circle-2 {
  width: 10rem;
}
.circle-3 {
  width: 10rem;
}
</style>

<script>
import Cart from '@/components/layout/CartModel.vue';
import Circle1 from '@/components/models/circle/CircleOne.vue';
import Circle2 from '@/components/models/circle/CircleTwo.vue';
import Circle3 from '@/components/models/circle/CircleThree.vue';

export default {
  data() {
    return {
      language: 'english',
      week: ['SUNDAY', 'MONDAY', 'TUESDAY', 'WEDNESDAY', 'THURSDAY', 'FRIDAY', 'SATURSAY'],
      dateYears: '',
      dateMonth: '',
      date: '',
      today: '',
    };
  },
  components: {
    Cart,
    Circle1,
    Circle2,
    Circle3,
  },
  methods: {
    getDate() {
      const newDate = new Date();
      this.dateYears = newDate.getFullYear();
      this.dateMonth = newDate.toLocaleString('en-US', { month: 'short' });
      this.date = newDate.getDate();
      this.today = newDate.getDay();
      this.week.forEach((item, i) => {
        if (i === this.today) {
          this.today = item;
        }
      });
    },
  },
  mounted() {
    this.getDate();
  },
};
</script>
