<!-- eslint-disable vuejs-accessibility/anchor-has-content -->
<template>
  <section class="">
    <!-- Modal -->
    <div
      class="modal fade"
      tabindex="-1"
      aria-labelledby="exampleModalLabe"
      aria-hidden="true"
      ref="cartModal"
    >
      <div class="modal-dialog rounded-0">
        <div class="modal-content">
          <div class="notepad content-box">
            <div class="top rounded-0 modal-header">
              <h1 class="modal-title fs-5">購物清單</h1>
              <button
                type="button"
                class="btn-close"
                data-bs-dismiss="modal"
                aria-label="Close"
              ></button>
            </div>
            <div class="paper d-flex flex-column justify-content-between">
              <div class="">
                <div class="row text-nowrap">
                  <span class="col-4">Name</span>
                  <span class="col-2">Price</span>
                  <span class="col-2">Qty</span>
                  <span class="col-2">Sub</span>
                  <span class="col-2">Edit</span>
                </div>
                <template v-for="(item, key) in carts.carts" :key="key">
                  <div class="row">
                    <span class="col-4">{{ item.product.title }}</span>
                    <span class="col-2">${{ item.product.price }}</span>
                    <span class="col-2">X{{ item.qty }}</span>
                    <span class="col-2">${{ item.qty * item.product.price }}</span>
                    <span class="col-2"
                      ><a href="#" @click.prevent="setData(item)"><i class="bi bi-pencil"></i></a
                    ></span>
                  </div>
                </template>
              </div>
              <div class="modal-footer pb-0 d-flex flex-column align-items-end">
                Total：${{ carts.total }}
                <div>
                  <router-link to="/products" class="text-decoration-none h6" @click="toggleModal"
                    >Shopping...</router-link
                  >
                  <router-link to="/checkout" class="text-decoration-none h6" @click="toggleModal"
                    >Checkout</router-link
                  >
                </div>
              </div>
            </div>
          </div>
          <div class="paperOverlay"></div>
        </div>
      </div>
    </div>
  </section>
  <EditCart ref="editcart" :tempData="tempData" @updata="updataCart" @openCart="toggleModal"/>
</template>

<style lang="scss" scoped>
