    $paper-line 31px,
    $paper-line 32px
  );
  font-family: 'Shadows Into Light', cursive;
  line-height: 32px;
  outline: 0;
  font-size: 22px;
}

.content-box {
  &:before,
  &:after {
    content: '';
    height: 2px;
    position: absolute;
    left: 0;
