</style>

<script>
import emitter from '@/assets/javascript/emitter';
import Modal from 'bootstrap/js/dist/modal';
import EditCart from '@/components/models/cart/EditCartModel.vue';

export default {
  data() {
    return {
      carts: [],
      cartModal: '',
      tempData: '',
    };
  },
  components: {
    EditCart,
  },
  methods: {
    getCarts() {
      const api = `${process.env.VUE_APP_API}/api/${process.env.VUE_APP_PATH}/cart`;
      this.$http.get(api).then((res) => {
        this.carts = res.data.data;
      });
    },
    updataCart(id, productId) {
      const api = `${process.env.VUE_APP_API}/api/${process.env.VUE_APP_PATH}/cart/${id}`;
      this.$http.put(api, { data: { product_id: productId, qty: 5 } }).then((res) => {
        console.log(res);
        this.openEditModal();
        this.getCarts();
        this.toggleModal();
      });
    },
    setData(item) {
      this.tempData = item;
      this.openEditModal();
    },
    toggleModal() {
      this.cartModal.toggle();
    },
    openEditModal() {
      this.$refs.editcart.toggleModal();
      this.toggleModal();
    },
  },
  mounted() {
    this.getCarts();
    this.cartModal = new Modal(this.$refs.cartModal);
    emitter.on('cartData', (e) => {
      this.carts = e;
    });
  },
};
</script>
